// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import Vuex from 'vuex'
import App from './App'
import axios from 'axios'
import VueAxios from 'vue-axios'
import VueRouter from 'vue-router'
import Todos from './components/Todos'
import Todo from './components/Todo'

Vue.use(VueAxios, axios)
var url='http://106.15.206.54:801/api'
Vue.use(VueRouter)
Vue.use(Vuex)
Vue.config.productionTip = false

// 定义路由
const routes = [
    {
        path: '/',
        component: Todos
    },
    {
        path: '/todo/:id',
        component: Todo,
        name:'todo'
    }
];

//创建 router 实例
const router = new VueRouter({
    // mode: 'history',
    routes // （缩写）相当于 routes: routes
});

//vuex
const store = new Vuex.Store({
    state: {
        todos: [],
        newTodo:{id: null, title: '', completed: false}
    },
    mutations: {
        get_todos_list (state,todos) {
            state.todos = todos
        },
        add_todo(state,todo){
            state.todos.push(todo);
        },
        delete_todo(state,index){
            state.todos.splice(index, 1);
        },
        complete_todo(state,todo){
            todo.completed = !todo.completed;
        },

    },
    actions:{
        getTodos(store){
            Vue.axios.get(url+'/todos').then(response => {
                store.commit('get_todos_list',response.data)
            })
        },
        addTodo(store,todo){
            Vue.axios.post(url+'/todos/create',{title:todo.title}).then(response=>{
                store.commit('add_todo',response.data)
            });
            store.state.newTodo = {id: null, title: '', completed: false};
        },
        deleteTodo(store,payload){
            Vue.axios.delete(url+'/todos/'+ payload.todo.id +'/delete').then(response=>{
                store.commit('delete_todo',payload.index)
            });
        },
        completeTodo(store,todo){
            Vue.axios.patch(url+'/todos/'+ todo.id +'/completed').then(response=>{
                store.commit('complete_todo',todo)
            });
        },

    }
})


/* eslint-disable no-new */
const app = new Vue({
    el: '#app',
    store,
    template: '<App/>',
    router,
    components: {App},
}).$mount('#app');




