<template>
    <div id="todos">
        <ul class="list-group">
            <li class="list-group-item" v-for="(todo,index) in todos"
                v-bind:class="{completed:todo.completed}">
                <router-link :to="{ name: 'todo', params: { id: todo.id }}">{{todo.title}}</router-link>
                <button class="btn btn-warning pull-right btn-xs" v-on:click="deleteTodo(todo,index)">Delete
                </button>
                <button class="btn pull-right btn-xs margin-right-5"
                        v-bind:class="[todo.completed?'btn-danger':'btn-success']"
                        v-on:click="completeTodo(todo)">{{todo.completed ? 'undo' : 'Complete'}}
                </button>
            </li>
        </ul>
        <todo-form></todo-form>
    </div>
</template>

<style>
    .completed {
        color: forestgreen;
        text-decoration-line: line-through;
    }

    .margin-right-5 {
        margin-right: 5px;
    }
    a {
        text-decoration:none;
        color: black;
    }
    a:hover, a:visited, a:link, a:active {
        text-decoration:none;
        color: black;
    }
</style>

<script>
    import TodoForm from './TodoForm'

    export default {
        name: 'todos',
        computed:{
            todos(){
                return this.$store.state.todos
            }
        },
        methods: {
            deleteTodo(todo,index) {
                this.$store.dispatch('deleteTodo',{todo,index})
            },
            completeTodo(todo) {
                this.$store.dispatch('completeTodo',todo)
            }
        },
        components: {
            TodoForm
        }
    }
</script>



